// /home/coffee/my-keymap-viewer/src/hooks/useKigoPractice3.ts
import { useMemo, useState, useEffect, useCallback, useRef } from 'react';
import { kigoPractice3Data, functionKeyMaps, kigoMapping3, EQUAL_KIGO_KEY_NAME } from '../data/keymapData';
import {
    KeyboardSide,
    KeyboardModel,
    PracticeHookProps,
    PracticeInputInfo,
    PracticeInputResult,
    PracticeHookResult,
    hid2GyouHRight_Kana,
    hid2GyouHLeft_Kana,
    hid2GyouVRight_Kana,
    hid2GyouVLeft_Kana,
    CharInfoKigo3,
    allKigo3CharInfos,
    PracticeHighlightResult,
    PracticeStatus, // PracticeStatus をインポート
    toHalfWidth, // toHalfWidth をインポート
} from './usePracticeCommons';

// 記号の基本練習３のステージ
type KigoPractice3Stage = 'kigoInput' | 'gyouInput' | 'waitingForSecondKigo';

const useKigoPractice3 = ({ gIdx, dIdx, isActive, side, kb, isRandomMode, layers }: PracticeHookProps): PracticeHookResult => {
    const [status, setStatus] = useState<PracticeStatus>('idle'); // status state を追加
    const [stage, setStage] = useState<KigoPractice3Stage>('kigoInput');
    const [showHighlightAfterWait, setShowHighlightAfterWait] = useState(true);
    const isWaitingForSecondKigo = useRef(false);
    const highlightTimeoutRef = useRef<number | null>(null);
    const prevGIdxRef = useRef(gIdx);
    const prevDIdxRef = useRef(dIdx);
    const isInitialMount = useRef(true);
    const prevIsActiveRef = useRef(isActive); // isActive の前回の値を保持
    const prevIsRandomModeRef = useRef(isRandomMode);

    const [randomTarget, setRandomTarget] = useState<CharInfoKigo3 | null>(null);

    // 通常モード用
    const currentGroup = useMemo(() => {
        if (isRandomMode || !isActive || gIdx < 0 || gIdx >= kigoPractice3Data.length) return null;
        return kigoPractice3Data[gIdx];
    }, [isActive, isRandomMode, gIdx]);

    const currentInputDef = useMemo(() => {
        if (isRandomMode || !currentGroup?.inputs || dIdx < 0 || dIdx >= currentGroup.inputs.length) {
            return null;
        }
        return currentGroup.inputs[dIdx];
    }, [isRandomMode, currentGroup, dIdx]);

    // ランダム/通常モード共通
    const isTargetEqualSign = useMemo(() => {
        if (isRandomMode) {
            // Directly check the character in random mode to ensure consistency
            return randomTarget?.char === '＝';
        }
        return !!currentInputDef && !currentInputDef.gyouKey;
    }, [isRandomMode, randomTarget, currentInputDef]);

    const currentTargetChar = useMemo(() => {
        if (isRandomMode) return randomTarget?.char ?? null;
        if (!currentGroup?.chars || dIdx < 0 || dIdx >= currentGroup.chars.length) return null;
        return currentGroup.chars[dIdx];
    }, [isRandomMode, randomTarget, currentGroup, dIdx]);

    const expectedGyouKey = useMemo(() => {
        if (isTargetEqualSign) return null;
        if (isRandomMode) return randomTarget?.gyouKey ?? null;
        return currentInputDef?.gyouKey ?? null;
    }, [isRandomMode, randomTarget, currentInputDef, isTargetEqualSign]);

    // ヘッダー文字
    const headingChars = useMemo(() => {
        if (!isActive) return [];
        let charsToDisplay: string[] = [];
        if (isRandomMode) {
            charsToDisplay = randomTarget ? [randomTarget.char] : [];
        } else {
            console.log(`[Kigo3 headingChars CALC] currentGroup?.chars:`, currentGroup?.chars);
            charsToDisplay = currentGroup?.chars ?? [];
        }
        // Transform the characters for display in the header
        return charsToDisplay.map(char => {
            if (char === ',') return '。';
            if (char === '.') return '、';
            return char;
        });
    }, [isActive, isRandomMode, randomTarget, currentGroup]);

    const selectNextRandomTarget = useCallback(() => {
        if (allKigo3CharInfos.length > 0) {
            const randomIndex = Math.floor(Math.random() * allKigo3CharInfos.length);
            const nextTarget = allKigo3CharInfos[randomIndex];
            setRandomTarget(nextTarget);
            setStage('kigoInput');
            isWaitingForSecondKigo.current = false;
            setShowHighlightAfterWait(true);
        } else {
            setRandomTarget(null);
        }
    }, [setRandomTarget, setStage]);

    // reset 関数
    const reset = useCallback(() => {
        setStage('kigoInput');
        isWaitingForSecondKigo.current = false;
        setShowHighlightAfterWait(true);
        if (highlightTimeoutRef.current !== null) {
            clearTimeout(highlightTimeoutRef.current);
            highlightTimeoutRef.current = null;
        }
        setRandomTarget(null);
        prevGIdxRef.current = -1;
        prevDIdxRef.current = -1;
        isInitialMount.current = true;
        prevIsRandomModeRef.current = false;
        setStatus('idle'); // reset 時は idle に
    }, [setStage, setRandomTarget]);

    useEffect(() => {
        if (!isActive && prevIsActiveRef.current) {
            reset();
        }

        if (isActive) {
            // isActive が true になったら、まず 'waiting' に設定する
            if (isActive && !prevIsActiveRef.current) {
                isInitialMount.current = true;
            }

            const indicesChanged = gIdx !== prevGIdxRef.current || dIdx !== prevDIdxRef.current;
            const randomModeChangedToTrue = isRandomMode && !prevIsRandomModeRef.current;
            const randomModeChangedToFalse = !isRandomMode && prevIsRandomModeRef.current;

            if (randomModeChangedToFalse || (!isRandomMode && (isInitialMount.current || indicesChanged))) {
                setStatus('waiting'); // 通常モード開始時は waiting に
                setStage('kigoInput');
                isWaitingForSecondKigo.current = false;
                setShowHighlightAfterWait(true);
                if (highlightTimeoutRef.current !== null) {
                    clearTimeout(highlightTimeoutRef.current);
                    highlightTimeoutRef.current = null;
                }
                setRandomTarget(null);
                prevGIdxRef.current = gIdx;
                prevDIdxRef.current = dIdx;
                isInitialMount.current = false;
            } else if (isRandomMode && (randomModeChangedToTrue || isInitialMount.current || !randomTarget)) {
                 setStatus('waiting'); // ランダムモード開始時も waiting に
                 selectNextRandomTarget();
                 isInitialMount.current = false;
                 prevGIdxRef.current = gIdx;
                 prevDIdxRef.current = dIdx;
            }
        }
        prevIsActiveRef.current = isActive;
        prevIsRandomModeRef.current = isRandomMode;
        // Always update prevGIdx/prevDIdx if active, so 'indicesChanged' is correct on next run
        if (isActive) {
            prevGIdxRef.current = gIdx;
            prevDIdxRef.current = dIdx;
        }

        return () => {
            if (highlightTimeoutRef.current !== null) {
                clearTimeout(highlightTimeoutRef.current);
                highlightTimeoutRef.current = null;
            }
        };
    }, [isActive, isRandomMode, gIdx, dIdx, randomTarget, reset, selectNextRandomTarget]);


    const handleInput = useCallback((inputInfo: PracticeInputInfo): PracticeInputResult => {
        if (inputInfo.type !== 'release') return { isExpected: true, shouldGoToNext: undefined }; // Press events are generally ignored for this practice's logic flow

        const { pressCode, layerIndex } = inputInfo;
        console.log(`[Kigo3 handleInput] Received release input: pressCode=${pressCode}, layerIndex=${layerIndex}, stage: ${stage}`);

        let isExpected = false;
        let shouldGoToNext_final: boolean | undefined = undefined;
        let nextStage: KigoPractice3Stage = stage;
        const layer8 = layers[8] ?? [];

        const expectedEqualKigoPressCode = layer8.findIndex(key => key.trim() === EQUAL_KIGO_KEY_NAME.trim());

        // ステージごとの入力処理
        if (stage === 'kigoInput') { // 「＝\n記号」キーの入力待ち
            if (layerIndex === 8 && pressCode === expectedEqualKigoPressCode && expectedEqualKigoPressCode !== -1) {
                isExpected = true;
                if (isTargetEqualSign) {
                    nextStage = 'waitingForSecondKigo';
                    isWaitingForSecondKigo.current = true;
                    if (highlightTimeoutRef.current !== null) clearTimeout(highlightTimeoutRef.current);
                    setShowHighlightAfterWait(false); // 1打目入力後、ハイライトを一旦非表示にする
                    highlightTimeoutRef.current = window.setTimeout(() => { setShowHighlightAfterWait(true); highlightTimeoutRef.current = null; }, 300);
                    shouldGoToNext_final = undefined;
                } else {
                    nextStage = 'gyouInput';
                    isWaitingForSecondKigo.current = false;
                    setShowHighlightAfterWait(false);
                    setStatus('waiting');
                    shouldGoToNext_final = undefined;
                }
            } else {
                setStatus('incorrect');
                isExpected = false;
                nextStage = 'kigoInput';
                shouldGoToNext_final = false;
            }
        } else if (stage === 'gyouInput') {
            if (layerIndex === 8) {
                const pressedKeyLabel = layer8[pressCode];
                let isInputCorrect = false;
                if (pressedKeyLabel && expectedGyouKey) { // expectedGyouKey を使用
                    const mainCharFromPressedKey = pressedKeyLabel.split('\n')[0].trim(); // 例: "。"
                    const normalizedPressedChar = toHalfWidth(mainCharFromPressedKey);    // 例: "。"
                    const normalizedTargetGyouKey = toHalfWidth(expectedGyouKey.trim());  // 例: "," (ターゲットが「。」の場合の内部表現)

                    console.log(`[Kigo3 handleInput] Stage gyouInput. NormalizedPressed: "${normalizedPressedChar}", NormalizedTargetGyouKey (internal): "${normalizedTargetGyouKey}"`);
                    if (
                        (normalizedTargetGyouKey === ',' && normalizedPressedChar === '。') ||
                        (normalizedTargetGyouKey === '.' && normalizedPressedChar === '、') ||
                        (normalizedPressedChar === normalizedTargetGyouKey) // その他の記号
                    ) {
                        isInputCorrect = true;
                    }
                }

                if (isInputCorrect) {
                    setStatus('correct');
                    isExpected = true;
                    nextStage = 'kigoInput';
                    if (isRandomMode) {
                        selectNextRandomTarget();
                        shouldGoToNext_final = undefined;
                    } else {
                        const currentGroupChars = currentGroup?.chars ?? [];
                        shouldGoToNext_final = (dIdx >= currentGroupChars.length - 1);
                    }
                    isWaitingForSecondKigo.current = false; setShowHighlightAfterWait(false);
                } else {
                    setStatus('incorrect');
                    isExpected = false;
                    nextStage = 'kigoInput';
                    shouldGoToNext_final = false;
                    isWaitingForSecondKigo.current = false; setShowHighlightAfterWait(false);
                }
            } else {
                setStatus('incorrect');
                isExpected = false;
                nextStage = 'kigoInput';
                shouldGoToNext_final = false;
                isWaitingForSecondKigo.current = false; setShowHighlightAfterWait(false);
            }
        } else if (stage === 'waitingForSecondKigo') {
            if (layerIndex === 8 && pressCode === expectedEqualKigoPressCode && expectedEqualKigoPressCode !== -1) {
                setStatus('correct');
                isExpected = true;
                nextStage = 'kigoInput';
                isWaitingForSecondKigo.current = false;
                setShowHighlightAfterWait(false);
                if (isRandomMode) {
                    selectNextRandomTarget();
                    shouldGoToNext_final = undefined;
                } else {
                    const currentGroupChars = currentGroup?.chars ?? [];
                    shouldGoToNext_final = (dIdx >= currentGroupChars.length - 1);
                }
            } else {
                setStatus('incorrect');
                isExpected = false;
                nextStage = 'kigoInput';
                isWaitingForSecondKigo.current = false;
                setShowHighlightAfterWait(false);
                shouldGoToNext_final = false;
            }
        }

        if (nextStage !== stage) {
            setStage(nextStage);
            console.log(`[Kigo3 handleInput] Stage changed from ${stage} to ${nextStage}`);
        }

        console.log(`[Kigo3 handleInput] Returning: isExpected=${isExpected}, shouldGoToNext=${shouldGoToNext_final}`);
        return { isExpected, shouldGoToNext: shouldGoToNext_final };

    }, [stage, layers, isTargetEqualSign, expectedGyouKey, currentGroup, dIdx, isRandomMode, selectNextRandomTarget, setStatus, setStage, setShowHighlightAfterWait, isWaitingForSecondKigo, highlightTimeoutRef]);

    const getHighlightClassName = useCallback((keyName: string, layoutIndex: number, keyIndex: number): PracticeHighlightResult => {
        const currentPracticeStatus = status;
        const originalKey = (keyName ?? '').trim();
        const layer8 = layers[8] ?? [];

        if (currentPracticeStatus === 'correct') {
            return { className: null, overrideKey: originalKey };
        }

        const expectedEqualKigoKeyIndex = layer8.findIndex(k => k.trim() === EQUAL_KIGO_KEY_NAME.trim());

        if (stage === 'kigoInput') {
            if (layoutIndex === 8 && keyIndex === expectedEqualKigoKeyIndex && expectedEqualKigoKeyIndex !== -1) {
                console.log(`[Kigo3 getHighlightClassName] Stage kigoInput. Highlighting ${originalKey} (keyIndex ${keyIndex})`);
                return { className: 'bg-blue-100 dark:bg-blue-800', overrideKey: null };
            }
        } else if (stage === 'gyouInput') {
            const normalizedExpectedGyouKey = expectedGyouKey ? toHalfWidth(expectedGyouKey.trim()) : null;
            const keyIndexForTargetGyou = normalizedExpectedGyouKey
                ? layer8.findIndex(layerKeyLabel => {
                    const mainCharFromLayer = layerKeyLabel.split('\n')[0].trim(); // 例: "。"
                    const normalizedMainCharFromLayer = toHalfWidth(mainCharFromLayer); // 例: "。"

                    // 「。」と「、」の特殊処理
                    if (normalizedExpectedGyouKey === ',' && normalizedMainCharFromLayer === '。') {
                        return true;
                    }
                    if (normalizedExpectedGyouKey === '.' && normalizedMainCharFromLayer === '、') {
                        return true;
                    }
                    return normalizedMainCharFromLayer === normalizedExpectedGyouKey; // その他の記号
                  })
                : -1;

            console.log(`[Kigo3 getHighlightClassName] Stage gyouInput. targetGyouKey: "${expectedGyouKey}" (normalized: "${normalizedExpectedGyouKey}"), found keyIndexForTargetGyou: ${keyIndexForTargetGyou}`);
            if (layoutIndex === 8 && keyIndex === keyIndexForTargetGyou && keyIndexForTargetGyou !== -1) {
                return { className: 'bg-blue-100 dark:bg-blue-800', overrideKey: null };
            }
        } else if (stage === 'waitingForSecondKigo') {
            if (layoutIndex === 8 && keyIndex === expectedEqualKigoKeyIndex && expectedEqualKigoKeyIndex !== -1) {
                console.log(`[Kigo3 getHighlightClassName] Stage waitingForSecondKigo. Highlighting ${originalKey} (keyIndex ${keyIndex}) with delay.`);
                return { className: showHighlightAfterWait ? 'bg-blue-100 dark:bg-blue-800' : null, overrideKey: null };
            }
        }

        if (layoutIndex === 8 && status !== 'incorrect') {
             let keyIndexForTargetGyou = -1;
             if (stage === 'gyouInput' && expectedGyouKey) {
                 const normalizedExpectedGyouKey = toHalfWidth(expectedGyouKey.trim());
                 keyIndexForTargetGyou = layer8.findIndex(layerKeyLabel => {
                    const mainCharFromLayer = layerKeyLabel.split('\n')[0].trim();
                    return toHalfWidth(mainCharFromLayer) === normalizedExpectedGyouKey;
                  });
             }
             const isCurrentTargetKey = (stage === 'kigoInput' || stage === 'waitingForSecondKigo') && keyIndex === expectedEqualKigoKeyIndex;
             const isGyouTargetKey = stage === 'gyouInput' && keyIndex === keyIndexForTargetGyou;
             if (!isCurrentTargetKey && !isGyouTargetKey) {
                 return { className: 'text-gray-700 dark:text-gray-300', overrideKey: originalKey };
             }
        }

        return { className: null, overrideKey: originalKey };
    }, [status, stage, layers, expectedGyouKey, showHighlightAfterWait]);

    const customizedDisplayLayers = useMemo(() => {
        if (!layers || layers.length <= 8) return layers;
        return layers.map((layer, index) => {
            if (index === 8) {
                return layer.map(key => {
                    const trimmedKey = key.trim();
                    if (trimmedKey === '。') return ',';
                    if (trimmedKey === '、') return '.';
                    return key;
                });
            }
            return layer;
        });
    }, [layers]);

    return {
        headingChars,
        handleInput,
        getHighlightClassName,
        reset,
        status,
        stage,
        targetChar: currentTargetChar,
        currentTarget: currentInputDef ? { type: 'kigo3', char: currentInputDef.char, isEqualSign: !currentInputDef.gyouKey, gyouKey: currentInputDef.gyouKey } : undefined,
        isInvalidInputTarget: () => false,
        targetLayerIndex: 8,
        targetLayerIndices: [8],
        displayLayers: layers, // Use original layers for this non-menu version
        errorHighlightLayer: null,
    };
};

export default useKigoPractice3;
